.profil-Container{
display: flex;
flex-direction: row;

}
.profil-L{
  width: 60%;
  padding-left: 40px;
  padding-top: 100px;
}
.profil-R{
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.welcome{
  position: absolute;
  top:110px;
  font-family: worksans;
  font-size: 60px;
  color: #ff5757;
}
.marahbe{
  position: absolute;
  top:50px;
  left: 170px;
  font-family: Arslan;
  font-style: italic;
  font-size: 110px;
  color: #37343e;
}
.imhassen{
  padding-top: 60px;
  font-family: oswald;
  font-size: 70px;
  color: #37343e;
}
.profil-desc{
  font-size: 20px;
}
.hassen-background{
  position: absolute;
  width: 500px;
  top:110px
}
.hassen-pic{
  position: relative;
  width: 400px;
  height: 500px;
  left: 50px;
}
.icons{
  display: flex;
  flex-direction: row;
  width: 50%;
  align-items: center;
  justify-content: center;
  padding-left: 45px;
}
